import React, { Component } from "react";
import { FaRegUserCircle, FaSuitcase } from "react-icons/fa";
import { GrStatusInfo } from "react-icons/gr";
import {
  AdminApplicationsListNew,
  ApplicationStatesDescription,
} from "../../../interfaces/application";
import noImage from "../../../assets/profile.png";
import DisplayCompetence from "../../Positions/DisplayCompetence";
import PositionDetails from "../../CandidateApplication/ApplicationComponents/PositionDetails";
import { Auth } from "../../../actions";
import { PositionAPI, SessionPlanInterface } from "../../../interfaces";

enum tabs {
  profile = "profile",
  position = "position",
}
interface ApplicationDetailsProps {
  selectedApplication: AdminApplicationsListNew;
  auth: Auth;
  positions: PositionAPI[];
  getDistrictName: (district_code: string) => string;
  sessionPlan: SessionPlanInterface[];
}

interface ApplicationDetailsState {
  loading: boolean;
  tab: tabs;
}

class ApplicationDetails extends Component<
  ApplicationDetailsProps,
  ApplicationDetailsState
> {
  constructor(props: ApplicationDetailsProps) {
    super(props);

    this.state = {
      loading: false,
      tab: tabs.profile,
    };
  }
  tabStatusClass = (status: boolean) => {
    return status === true
      ? "col-span-6 bg-white py-2 px-3 cursor-pointer text-lg bg-blue-600 text-white rounded-md font-bold flex flex-row items-center justify-start bg-blue-50 gap-3 animate__animated animate__fadeIn"
      : "col-span-6 bg-white bg-blue-50 hover:bg-blue-100 py-2 px-3 cursor-pointer text-lg border-b-4 border-blue-200 text-gray-600 hover:text-blue-700 flex flex-row items-center justify-start gap-3";
  };
  render() {
    const selectedPosition = this.props.positions.find(
      (itm) => itm.position_id === this.props.selectedApplication.position
    );

    return (
      <div className="px-2 py-2 border-t">
        {/* Tabs */}
        <div className="rounded grid md:grid-cols-12">
          <div
            onClick={() => this.setState({ tab: tabs.profile })}
            className={`${
              this.state.tab === tabs.profile
                ? this.tabStatusClass(true)
                : this.tabStatusClass(false)
            }`}
          >
            <FaRegUserCircle className="text-3xl" />
            Application details
          </div>
          <div
            onClick={() => this.setState({ tab: tabs.position })}
            className={`${
              this.state.tab === tabs.position
                ? this.tabStatusClass(true)
                : this.tabStatusClass(false)
            }`}
          >
            <FaSuitcase className="text-3xl" />
            Position details
          </div>
        </div>
        {/* Tab contents */}
        <div className="border rounded px-4 py-4 mt-3">
          {this.state.tab === tabs.profile ? (
            <div className="grid md:grid-cols-12">
              <div className="col-span-6">
                <div className="text-2xl font-bold mb-6">
                  General information
                </div>
                {/* <div className="">
                  <div className="px-2 py-2">
                    <img
                      src={noImage}
                      alt=""
                      className="h-28 w-28 rounded-full mb-2"
                    />
                  </div>
                </div> */}
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">First name: </span>
                  <span className="font-bold">
                    {this.props.selectedApplication.fname}
                  </span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">Last name: </span>
                  <span className="font-bold">
                    {this.props.selectedApplication.lname}
                  </span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">National ID: </span>
                  <span className="font-bold">
                    {this.props.selectedApplication.n_id}
                  </span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">Gender: </span>
                  <span className="font-bold">
                    {/* {this.props.selectedApplication.sex} */}M
                  </span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">Phone number: </span>
                  <span className="font-bold">
                    {this.props.selectedApplication.phone_number}
                  </span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">Email: </span>
                  <span className="font-bold">
                    {this.props.selectedApplication.email}
                  </span>
                </div>
                <div className="flex flex-row item-center justify-left gap-3 mb-2">
                  <span className="text-gray-500">Resident district: </span>
                  <span className="font-bold">
                    {this.props.getDistrictName(
                      this.props.selectedApplication.district_of_residence_id
                    )}
                  </span>
                </div>
              </div>
              {/* Application details */}
              <div className="col-span-6">
                <div className="mb-4 font-extrabold text-2xl">
                  Application details
                </div>
                <div className="flex flex-col w-full rounded-md bg-gray-100 hover:bg-blue-100 p-3">
                  <span className="text-gray-500">Application session</span>
                  <span className="font-bold uppercase">
                    {
                      this.props.sessionPlan.find(
                        (itm) =>
                          itm._id ===
                          this.props.selectedApplication.application_session
                      )?.session_plan_name
                    }
                    (
                    {
                      this.props.sessionPlan.find(
                        (itm) =>
                          itm._id ===
                          this.props.selectedApplication.application_session
                      )?.period
                    }
                    )
                  </span>
                </div>
                <div className="flex flex-col w-full rounded-md bg-gray-100 hover:bg-blue-100 p-3 mt-2">
                  <span className="text-gray-500">Application date</span>
                  <span className="font-bold">
                    {new Date(
                      this.props.selectedApplication.createdAt
                    ).toString()}
                  </span>
                </div>
                <div className="flex flex-col w-full rounded-md bg-gray-100 hover:bg-blue-100 p-3 mt-2">
                  <span className="text-gray-500">Application status</span>
                  <span className="font-bold uppercase">
                    {this.props.selectedApplication.application_status}
                  </span>
                </div>
              </div>
              <div className="col-span-12">
                <div className="flex flex-col w-full mt-2 gap-3 border-2 border-blue-600 rounded-md p-4">
                  <div className="text-2xl font-extrabold -mt-2">
                    Placement Preferences
                  </div>
                  <div className="grid grid-cols-12 gap-3">
                    {this.props.selectedApplication.contract === null ? (
                      <div className="col-span-12 bg-red-100 px-3 py-2 text-left w-full text-yellow-700">
                        No preferences found!
                      </div>
                    ) : this.props.selectedApplication.contract.district.values
                        .length === 0 ? (
                      <div className="col-span-12 bg-green-100 text-green-800 font-bold px-3 py-2 rounded">
                        All districts
                      </div>
                    ) : (
                      this.props.selectedApplication.contract.district.values.map(
                        (item, i) => (
                          <div
                            className={`${
                              this.props.selectedApplication.contract !==
                                null &&
                              this.props.selectedApplication.contract.district
                                .strict === false
                                ? "col-span-4"
                                : "col-span-6"
                            } flex flex-row items-center gap-2`}
                            key={i + 1}
                          >
                            <div>
                              <div className="h-8 w-8 rounded-full bg-blue-600 text-white flex items-center justify-center">
                                {i + 1}
                              </div>
                            </div>
                            <div>{this.props.getDistrictName(item)}</div>
                          </div>
                        )
                      )
                    )}
                    {this.props.selectedApplication.contract !== null &&
                      this.props.selectedApplication.contract.district
                        .strict === false && (
                        <div className="col-span-4">
                          <div className="bg-yellow-100 text-yellow-900 rounded-full px-2 w-max mt-1 font-bold">
                            And other districts
                          </div>
                        </div>
                      )}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            selectedPosition !== undefined && (
              <PositionDetails
                positionDetails={selectedPosition}
                cancel={() => {}}
                apply={() => {}}
                auth={this.props.auth}
                showRegister={(status: boolean) => {}}
                hideTools={true}
              />
            )
          )}
        </div>
      </div>
    );
  }
}

export default React.memo(ApplicationDetails);
