export const ignoreThisApplicationStatus: ApplicationStates[] = [
  ApplicationStates.Failed,
  ApplicationStates.Pass,
  // ApplicationStates.Rejected,
];

export enum reportContentKeyInterface {
  "No" = "No",
  "Names" = "Names",
  "Gender" = "Gender",
  "NationalID" = "NationalID",
  "Email" = "Email",
  "Phone" = "Phone",
  "Residence" = "Residence",
  "Session" = "Session",
  "Institution" = "Institution",
  "Date" = "Date",
  "Decision" = "Decision",
  "Position" = "Position",
  "Preferences" = "Preferences",
}

export interface reportContentInterface {
  key: reportContentKeyInterface;
  title: string;
  status: boolean;
}

const defaultReportContent: reportContentInterface[] = [
  {
    key: reportContentKeyInterface.Names,
    title: "Applicant Names",
    status: true,
  },
  {
    key: reportContentKeyInterface.NationalID,
    title: "National ID",
    status: true,
  },
  { key: reportContentKeyInterface.Gender, title: "Gender", status: true },
  { key: reportContentKeyInterface.Phone, title: "Phone", status: true },
  {
    key: reportContentKeyInterface.Session,
    title: "Application Period",
    status: true,
  },
  {
    key: reportContentKeyInterface.Institution,
    title: "Education Institute",
    status: true,
  },
  {
    key: reportContentKeyInterface.Date,
    title: "Application Date",
    status: true,
  },
  { key: reportContentKeyInterface.Decision, title: "Decision", status: true },
  {
    key: reportContentKeyInterface.Position,
    title: "Position Title",
    status: true,
  },
  {
    key: reportContentKeyInterface.Preferences,
    title: "Preferences",
    status: true,
  },
];

interface loadAllApplicationsProps {
  auth: Auth;
  positions: PositionAPI[] | null;
  sessionPlan: SessionPlanReducerInterface;
  schoolLevels: schoolLevelsStoreInterface;

  FC_GetAllSessionByInstitution: Function;

  FC_GetPositionsByInstitution: Function;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface loadAllApplicationsState {
  loading: boolean;
  selectedEducationCategory: education_category | "all";
  defaultSelectedPeriod: SessionPlanInterface | null;
  applicationListData: AdminApplicationsListNew[] | null;
  selectedApplicationStatus: ApplicationStates | "ALL" | null;
  selected_period: SessionPlanInterface | null;
  selectPosition: boolean;
  selectPeriod: boolean;
  applicationDetailsModal: {
    selected_application: AdminApplicationsListNew;
    status: boolean;
  } | null;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  searchInput: string | "";
  searchPositionInput: string | "";
  applicationsToPrint: AdminApplicationsToPrintInterface[] | null;
  reportTitle: string;
  reportContent: reportContentInterface[];
  selectAll: boolean;
  formError: {
    element: string;
    msg: string;
  } | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  downloading: boolean;
  selected_position: PositionAPI | null;
  districts: LocationAPI | null;
}

class _Applications extends Component<
  loadAllApplicationsProps,
  loadAllApplicationsState
> {
  constructor(props: loadAllApplicationsProps) {
    super(props);

    this.state = {
      loading: false,
      defaultSelectedPeriod: null,
      selectedEducationCategory: "all",
      selectedApplicationStatus: "ALL",
      applicationListData: null,
      selectPosition: false,
      selectPeriod: false,
      applicationDetailsModal: null,
      generateReportModal: null,
      searchInput: "",
      searchPositionInput: "",
      reportTitle: "Application Report",
      selectAll: true,
      applicationsToPrint: null,
      formError: null,
      reportContent: defaultReportContent,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      selected_period: null,
      downloading: false,
      selected_position: null,
      districts: null,
    };
  }

  setLoadingState = (state: boolean) => {
    this.setState({ loading: state });
  };

  loadAllApplications = async (
    position: PositionAPI | null,
    period: SessionPlanInterface | null,
    callback: (loading: boolean, msg: string) => void
  ) => {
    if (position !== null && period !== null) {
      callback(true, "");
      try {
        const res = await axios.get<AdminApplicationsListNew[]>(
          `${API.APPLICATION}/apply/updated/${position.position_id}/${period._id}`
        );
        this.setState({ applicationListData: res.data });
        console.log("applicationListData: ", res);
        callback(false, "");
      } catch (error: any) {
        console.error({ error_found: error });
        // callback(false, errorToText(error));
      }
    }
  };

  FD_GetSessions = (institution_id: string) => {
    this.props.FC_GetAllSessionByInstitution(
      institution_id,
      (status: boolean, msg: string, data?: SessionPlanInterface[]) => {
        this.setLoadingState(status);
        if (!status) {
          if (data && data.length > 0) {
            if (this.state.selected_period === null) {
              let active_period = data.find((item) => item.is_active);
              if (active_period) {
                this.setState({ selected_period: active_period });
                this.setState({
                  selectPeriod: false,
                  defaultSelectedPeriod: active_period,
                });
              } else if (data.length > 0) {
                active_period = data[data.length - 1];
                this.setState({ selected_period: active_period });
                this.setState({
                  selectPeriod: false,
                  defaultSelectedPeriod: active_period,
                });
              } else {
                this.setState({ selectPeriod: true });
              }
            }
          }
        }
      }
    );
  };

  FC_OnInstitutionChanged = (institution_id: string) => {
    // LOAD POSITIONS BY INSTITUTION =================
    this.props.FC_GetPositionsByInstitution(
      institution_id,
      (status: boolean, msg: string) => {
        this.setLoadingState(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        } else if (status === false) {
          this.setState({ selectPosition: true });
        }
      }
    );

    // LOAD SESSION BY INSTITUTION =================
    this.props.FC_GetAllSessionByInstitution(
      institution_id,
      (
        status: boolean,
        errorMsg: string,
        sessionData: SessionPlanInterface[]
      ) => {
        this.setLoadingState(status);
        if (!status) {
          if (!isEmptyOrSpaces(errorMsg)) {
            this.props.PushPopupAlert({
              title: errorMsg,
              theme: PopupAlertTheme.warning,
            });
          }

          if (sessionData && sessionData.length > 0) {
            let activeSession = sessionData.find((item) => item.is_active);
            if (activeSession) {
              this.setState({ selected_period: activeSession });
              // if (this.state.reportMainData === null) {
              //   this.loadReportMainData(activeSession);
              // }
            } else {
              this.setState({ selectPeriod: true });
            }
          }
        }
      }
    );
  };

  GetDistrictName = (district_code: string): string => {
    let district = this.state.districts?.districts.find(
      (itm) => itm.district_code.toString() === district_code.toString()
    );
    if (district === undefined) {
      return "";
    } else {
      return district.district_name;
    }
  };

  componentDidMount() {
    // Get location
    if (this.state.districts === null) {
      this.setState({ loading: true });
      FC_getLocations(
        (error: boolean, locations: LocationAPI | null, msg: string) => {
          if (locations !== null) {
            this.setState({
              districts: locations,
              loading: false,
            });
          } else {
            if (error === false) {
              alert(errorToText(error));
              this.setState({ loading: false, districts: null });
            }
          }
        }
      );
    }
    if (FC_HasInstitute(this.props.auth.education_institution_id) === true) {
      this.FC_OnInstitutionChanged(this.props.auth.education_institution_id!);
    } else {
      if (
        this.props.schoolLevels.education_categories === null ||
        this.props.schoolLevels.education_categories.length
      ) {
        this.props.loadEducationCategories(
          (status: boolean, data?: education_category[]) => {
            this.setLoadingState(status);
            if (!status && data && data.length > 0) {
              this.setState({ selectedEducationCategory: data[0] });
              if (
                FC_HasInstitute(this.props.auth.education_institution_id) ===
                false
              ) {
                this.FC_OnInstitutionChanged(data[0].education_institute_id);
              } else {
                this.FC_OnInstitutionChanged(
                  this.props.auth.education_institution_id!
                );
              }
            }
          }
        );
      } else if (!this.props.schoolLevels.selectedEducationCategory) {
        this.setState({
          selectedEducationCategory:
            this.props.schoolLevels.education_categories[0],
        });
        this.FC_OnInstitutionChanged(
          this.props.schoolLevels.education_categories[0].education_institute_id
        );
      }
    }
  }

  componentDidUpdate(
    prevProps: loadAllApplicationsProps,
    prevState: loadAllApplicationsState
  ) {
    if (prevState.loading !== this.state.loading) {
      // UPDATE APPLICATION LIST FOR UPDATE ==============================
      this.updateApplicationToPrint();
    }
  }

  AdminApplicationStatuses = (
    application: AdminApplicationsListNew[]
  ): { title: string; count: number; status: ApplicationStates | "ALL" }[] => {
    let output: {
      title: string;
      count: number;
      status: ApplicationStates | "ALL";
    }[] = [{ title: "All", count: application.length, status: "ALL" }];
    for (let i in ApplicationStatesReport) {
      if (
        !ignoreThisApplicationStatus.includes(ApplicationStatesReport[i].key)
      ) {
        output.push({
          title: ApplicationStatesReport[i].caption,
          count: application.filter(
            (itm) => itm.application_status === ApplicationStatesReport[i].key
          ).length,
          status: ApplicationStatesReport[i].key,
        });
      }
    }
    // console.log("=================AdminApplicationStatuses===================");
    // console.log({ output, application });
    // console.log("=================AdminApplicationStatuses===================");
    return output;
  };

  filteredApplications = (
    applications: AdminApplicationsListNew[] | null,
    Status: ApplicationStates | "ALL" | null,
    education_category: education_category | "all"
  ) => {
    let appl: AdminApplicationsListNew[] | null = applications;
    if (education_category !== "all" && applications !== null) {
      appl = applications.filter(
        (itm) =>
          itm.application_session === education_category.session_plan?._id
      );
    }
    if (Status === null || Status === "ALL") {
      return appl;
    } else {
      if (appl === null) {
        return appl;
      } else {
        return appl.filter((item) => item.application_status === Status);
      }
    }
  };

  setDownloading = (status: boolean) => {
    this.setState({ downloading: status });
  };

  updateApplicationToPrint = () => {
    if (this.state.selected_position !== null)
      this.setState({
        reportTitle: this.state.selected_position.position_title,
      });
    if (this.state.applicationListData) {
      let arrayNew: AdminApplicationsToPrintInterface[] = [];
      let n: number = 1;
      for (const key in this.state.applicationListData) {
        if (
          Object.prototype.hasOwnProperty.call(
            this.state.applicationListData,
            key
          ) &&
          this.state.selected_position !== null
        ) {
          const element = this.state.applicationListData[key];
          let dataArray: AdminApplicationsToPrintInterface = {
            No: n,
            Names: element.fname + " " + element.lname,
            NationalID: element.n_id,
            Gender: element.gender === "M" ? "Male" : "Female",
            Position: this.state.selected_position.position_title,
            Phone: element.phone_number,
            Email: element.email,
            Institution:
              this.state.selected_position.education_institute_id
                .education_category_title,
            Session: this.props.sessionPlan.allSessionPlans?.find(
              (itm) => itm._id === element.application_session
            )?.session_plan_name,
            Residence: this.GetDistrictName(element.district_of_residence_id),
            Preferences: `${
              element.contract === null
                ? ""
                : element.contract.district.values?.length === 0
                ? "All districts"
                : element.contract.district.values.map((val) => {
                    return `${this.GetDistrictName(val)}`;
                  })
            }${
              element.contract !== null &&
              element.contract.district.strict === false &&
              element.contract.district.values.length > 0
                ? ", and other districts"
                : ""
            }`,
            Date: new Date(element.createdAt).toString(),
            Decision: element.application_status,
          };

          for (const key in this.state.reportContent) {
            if (
              Object.prototype.hasOwnProperty.call(
                this.state.reportContent,
                key
              )
            ) {
              if (!this.state.reportContent[key].status) {
                delete dataArray[this.state.reportContent[key].key];
              }
            }
          }
          arrayNew.push(dataArray);
          n++;
        }
      }
      if (
        this.state.selectedApplicationStatus !== null &&
        this.state.selectedApplicationStatus !== "ALL"
      )
        arrayNew = arrayNew.filter(
          (item) => item.Decision === this.state.selectedApplicationStatus
        );
      this.setState({ applicationsToPrint: arrayNew });
      arrayNew = [];
      n = 1;
      // this.props.applicationsToPrint=;
    }
  };

  updateReportContent = (key: reportContentKeyInterface) => {
    let index: number = this.state.reportContent.findIndex(
      (item) => item.key === key
    );
    let newArray: reportContentInterface[] = this.state.reportContent;
    newArray[index].status = !newArray[index].status;
    this.setState({ reportContent: newArray });
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setState({ selectAll: false });
    } else {
      this.setState({ selectAll: true });
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateApplicationToPrint();
  };

  generateThisPDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    if (this.state.selected_position && this.state.selected_period) {
      const table: HTMLElement | null =
        document.getElementById("report_content");
      const reportSettings: reportSettingsInterface = {
        doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
        title: "Applications Report",
        subjectTitle: "Position",
        subject: this.state.selected_position.position_title,
        period: this.state.selected_period.session_plan_name,
        footer: "Online Recruitment",
        institute:
          this.state.selectedEducationCategory === "all"
            ? "All institute"
            : this.state.selectedEducationCategory.education_category_title,
        orientation: "l",
      };
      console.log("====================================");
      console.log({ table });
      console.log("====================================");
      table && generatePDF(reportSettings, table, this.setDownloading);
    }
  };

  isChangeAllowed = (key: reportContentKeyInterface): boolean => {
    if (
      key === reportContentKeyInterface.Names ||
      key === reportContentKeyInterface.Decision ||
      key === reportContentKeyInterface.Session
    ) {
      return false;
    }
    return true;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: reportContentInterface[] = this.state.reportContent;
    for (const key in newArray) {
      if (this.isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    this.setState({
      reportContent: newArray,
      selectAll: !currentState,
    });

    this.updateApplicationToPrint();
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setState({
        sortProperty: thisProperty,
        isSorted: true,
        sortOrder: "DESC",
      });
    } else {
      this.setState({
        sortProperty: thisProperty,
        isSorted: true,
        sortOrder: "ASC",
      });
    }
  };

  render() {
    return (
      <>
        <SideNavigation>
          {FC_HasInstitute(this.props.auth.education_institution_id) ===
            false && (
            <div className="flex flex-col mb-4">
              <span className="text-sm text-gray-600 mb-2">
                Education categories:
              </span>
              {this.props.schoolLevels.education_categories === null ? (
                <Loading />
              ) : this.props.schoolLevels.education_categories.length === 0 ? (
                <span>No result found</span>
              ) : (
                <section>
                  {/* <div
                    onClick={() => {
                      this.setState({ selectedEducationCategory: "all" });
                    }}
                    className={`border rounded px-2 py-2 mb-2 cursor-pointer ${
                      this.state.selectedEducationCategory === "all"
                        ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                        : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                    } font-bold flex flex-row items-center gap-2`}
                  >
                    {this.state.selectedEducationCategory === "all" ? (
                      <MdRadioButtonChecked />
                    ) : (
                      <MdRadioButtonUnchecked />
                    )}
                    <span>ALL</span>
                  </div> */}
                  {this.props.schoolLevels.education_categories.map(
                    (item, i) => (
                      <div
                        onClick={() => {
                          this.setState({ selectedEducationCategory: item });
                          this.FC_OnInstitutionChanged(
                            item.education_institute_id
                          );
                        }}
                        key={i + 1}
                        className={`border rounded px-2 py-2 mb-2 cursor-pointer ${
                          this.state.selectedEducationCategory !== "all" &&
                          this.state.selectedEducationCategory
                            .education_institute_id ===
                            item.education_institute_id
                            ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                            : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                        } font-bold flex flex-row items-center gap-2`}
                      >
                        {this.state.selectedEducationCategory !== "all" &&
                        this.state.selectedEducationCategory
                          .education_institute_id ===
                          item.education_institute_id ? (
                          <MdRadioButtonChecked />
                        ) : (
                          <MdRadioButtonUnchecked />
                        )}
                        <span>{item.education_category_title}</span>
                      </div>
                    )
                  )}
                </section>
              )}
            </div>
          )}
          <div className="">
            <div className="text-gray-600 text-sm mb-2">Selected period:</div>
            <div
              onClick={() => this.setState({ selectPeriod: true })}
              className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline  text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {this.state.selected_period !== null
                ? capitalizeFirstWord(
                    this.state.selected_period.session_plan_name
                  )
                : "Filter by period"}
              <FiEdit className="text-3xl text-blue-600" />
            </div>
          </div>
          {/* <Search data={[1, 2, 3]} /> */}
          {this.state.selected_position && (
            <div className="pt-1">
              <div className="text-sm text-gray-700 pb-2 pt-2">
                Application statuses:
              </div>
              {this.state.applicationListData === null ||
              this.state.districts === null ? (
                <Loading />
              ) : (
                this.AdminApplicationStatuses(
                  this.state.applicationListData
                ).map(
                  (
                    itm: {
                      title: string;
                      count: number;
                      status: ApplicationStates | "ALL";
                    },
                    i: number
                  ) => (
                    // itm.count > 0 &&
                    <Fragment key={itm.title}>
                      <div
                        onClick={() =>
                          this.setState({
                            selectedApplicationStatus: itm.status,
                          })
                        }
                      >
                        <MainItem
                          title={itm.title}
                          count={itm.count}
                          active={
                            this.state.selectedApplicationStatus === itm.status
                              ? true
                              : false
                          }
                        />
                      </div>
                    </Fragment>
                  )
                )
              )}
            </div>
          )}
        </SideNavigation>

        {/* content goes here ====================== */}
        <section>
          {/* REPORT TITLE ========================================= */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-8">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-light">Applications Report</span>
                <div
                  onClick={() => this.setState({ selectPosition: true })}
                  className="flex flex-row gap-2 items-end"
                >
                  {/* <span className="text-gray-500 text-xl">Position:</span> */}
                  <span className="text-blue-600 font-bold underline cursor-pointer text-xl">
                    {this.state.selected_position !== null
                      ? this.state.selected_position.position_title
                      : "No position selected, Click to select"}
                  </span>
                </div>
              </div>
            </div>
            <div className="col-span-4">
              <div className="float-right mt-2">
                {this.state.applicationsToPrint &&
                  this.state.applicationsToPrint.length > 0 && (
                    <div className="flex flex-row items-center justify-end font-bold mb-2">
                      <BiExport className="text-2xl text-gray-500" />
                      <span className="text-gray-500">Export as</span>
                      {/* EXCEL BUTTON ================= */}
                      <button
                        className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 ${
                          this.state.downloading && "cursor-wait"
                        }`}
                        disabled={this.state.loading || this.state.downloading}
                        onClick={() =>
                          this.setState({
                            generateReportModal: {
                              reportType: "EXCEL",
                              status: true,
                            },
                          })
                        }
                      >
                        EXCEL
                      </button>
                      {/* PDF BUTTON ================= */}
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        onClick={() => this.generateThisPDF()}
                        disabled={this.state.loading}
                      >
                        PDF
                      </button>
                    </div>
                  )}
                <div
                  onClick={() => this.setState({ selectPosition: true })}
                  className="btn bg-blue-50 border border-blue-300 text-blue-600 cursor-pointer hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm "
                >
                  <BiFilterAlt className="text-xl" />
                  Filter by position
                </div>
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ========================================= */}
          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setState({ searchInput: e.target.value })}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded"
              />
            </div>
            <div className="w-full overflow-x-auto">
              <table className="w-full shadow" id="report_content">
                <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                  <tr>
                    <td className="px-2 py-2">#</td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("fname")}
                    >
                      <div className="flex items-center gap-1">
                        Applicant names <BiSort />
                      </div>
                    </td>
                    <td className="px-2 py-2">National ID</td>
                    <td className="px-2 py-2">Gender</td>
                    <td className="px-2 py-2">Phone</td>
                    <td className="px-2 py-2">Position</td>
                    <td className="px-2 py-2">Res. District</td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("application_status")}
                    >
                      <div className="flex items-center gap-1">
                        Status <BiSort />
                      </div>
                    </td>
                    <td className="px-2 py-2 cursor-pointer">
                      <div className="flex items-center gap-1">Preferences</div>
                    </td>
                    <td className="px-2 py-2">Date</td>
                  </tr>
                </thead>
                <tbody>
                  {this.state.selected_position === null ? (
                    <tr>
                      <td colSpan={10} className="px-2 py-2">
                        <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                          <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                          <span className="text-sm font-bold">
                            No position selected! Please select position to get
                            data
                          </span>
                          <div className="w-full mt-2 flex flex-row items-center justify-center">
                            <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                              <li
                                onClick={() =>
                                  this.setState({ selectPosition: true })
                                }
                                className="underline text-blue-600 cursor-pointer"
                              >
                                Choose position
                              </li>
                            </ul>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ) : this.state.applicationListData === null ||
                    this.filteredApplications(
                      this.state.applicationListData,
                      this.state.selectedApplicationStatus,
                      this.state.selectedEducationCategory
                    ) === null ? (
                    <tr>
                      <td colSpan={10} className="px-2 py-2">
                        <Loading />
                      </td>
                    </tr>
                  ) : this.filteredApplications(
                      this.state.applicationListData,
                      this.state.selectedApplicationStatus,
                      this.state.selectedEducationCategory
                    )!.length === 0 ? (
                    <tr>
                      <td colSpan={10} className="px-2 py-2">
                        <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                          <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                          <span className="text-sm font-bold">
                            No result found! Please try to search again
                          </span>
                          <div className="w-full mt-2 flex flex-row items-center justify-center">
                            <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                              <li
                                onClick={() =>
                                  this.setState({ selectPeriod: true })
                                }
                                className="underline text-gray-500 cursor-pointer"
                              >
                                Filter by period
                              </li>
                              <li className="text-gray-400 italic">or</li>
                              <li
                                onClick={() =>
                                  this.setState({ selectPosition: true })
                                }
                                className="underline text-blue-600 cursor-pointer"
                              >
                                Filter by position
                              </li>
                            </ul>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ) : (
                    sortThisArray(
                      this.state.isSorted,
                      searchData(
                        this.filteredApplications(
                          this.state.applicationListData,
                          this.state.selectedApplicationStatus,
                          this.state.selectedEducationCategory
                        )!,
                        this.state.searchInput
                      ),
                      this.state.sortProperty,
                      this.state.sortOrder
                    ).map((item: AdminApplicationsListNew, i: number) => (
                      <tr
                        onClick={() =>
                          this.setState({
                            applicationDetailsModal: {
                              selected_application: item,
                              status: true,
                            },
                          })
                        }
                        key={i + 1}
                        className={`${
                          i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                        } text-gray-600 hover:bg-blue-100 hover:text-blue-700 cursor-pointer border-b border-gray-300 hover:border-blue-500 text-sm`}
                      >
                        <td className="px-2 py-2">{i + 1}</td>
                        <td className="px-2 py-2">
                          {item.fname} {item.lname}
                        </td>
                        <td className="px-2 py-2">{item.n_id}</td>
                        <td className="px-2 py-2">
                          {item.gender === "M" ? "Male" : "Female"}
                        </td>
                        <td className="px-2 py-2">{item.phone_number}</td>
                        <td className="px-2 py-2">
                          {this.state.selected_position?.position_title}
                        </td>
                        <td className="px-2 py-2">
                          {this.GetDistrictName(item.district_of_residence_id)}
                        </td>
                        <td className="px-2 py-2">
                          {capitalizeFirstLetter(item.application_status)}
                        </td>
                        <td className="px-2 py-2">
                          {item.contract === null
                            ? ""
                            : item.contract.district.values?.length === 0
                            ? "All districts"
                            : item.contract.district.values.map((val) => {
                                return `${this.GetDistrictName(val)}${
                                  item.contract !== null &&
                                  item.contract.district.values.length > 0 &&
                                  item.contract.district.values[
                                    item.contract.district.values.length - 1
                                  ] !== val
                                    ? ", "
                                    : ""
                                }`;
                              })}
                          {item.contract !== null &&
                          item.contract.district.strict === false &&
                          item.contract.district.values.length > 0
                            ? ", and other districts"
                            : ""}
                        </td>
                        <td className="px-2 py-2">{DATE(item.createdAt)}</td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </section>

        {/* PERIOD && POSITION CONTENT MODAL */}
        {(this.state.selectPosition === true ||
          this.state.selectPeriod === true) && (
          <Modal
            title={
              <div className="flex flex-row gap-4 items-center">
                {this.state.selectPeriod === true ? (
                  <MdHistory className="text-5xl text-blue-600" />
                ) : (
                  <IoFilter className="text-5xl text-blue-600" />
                )}
                <div className="flex flex-col">
                  <span className="font-bold text-xl text-blue-600">
                    {this.state.selectPeriod === true
                      ? "Choose a period"
                      : "Filter by a position"}
                  </span>
                </div>
              </div>
            }
            backDrop={true}
            theme={Themes.default}
            close={() =>
              this.setState({
                selectPosition: false,
                selectPeriod: false,
                searchPositionInput: "",
              })
            }
            backDropClose={true}
            widthSizeClass={ModalSize.extraLarge}
            displayClose={true}
            padding={{ title: true }}
          >
            <div className="px-2 pb-2">
              {this.state.selectPeriod === true ? (
                <ChooseSession
                  allSession={this.props.sessionPlan.allSessionPlans}
                  setSelectSession={(value: SessionPlanInterface | "all") => {
                    if (value !== "all") {
                      this.setState({ selected_period: value });
                      this.loadAllApplications(
                        this.state.selected_position,
                        value,
                        (status: boolean, errorMessage: string) => {
                          this.setLoadingState(status);
                          if (!isEmptyOrSpaces(errorMessage)) {
                            this.props.PushPopupAlert({
                              title: errorMessage,
                              theme: PopupAlertTheme.warning,
                            });
                          }
                        }
                      );
                      this.setState({
                        selectPeriod: false,
                        applicationListData: null,
                      });
                    }
                  }}
                  chooseAll={false}
                  currentSelected={
                    this.state.selected_period
                      ? this.state.selected_period
                      : "all"
                  }
                />
              ) : this.state.selectPosition === true &&
                this.props.positions === null ? (
                <LoadingV2 type={LoadingType.dark} />
              ) : this.props.positions!.length === 0 ? (
                <div className="flex flex-col text-gray-400 items-center justify-center">
                  <MdSearch className="text-6xl" />
                  <div className="flex flex-col items-center justify-center">
                    <span>No positions found! Please go back!</span>
                    <Link
                      to="/"
                      className="btn bg-yellow-100 text-yellow-700 my-4 hover:bg-yellow-200"
                    >
                      Go back
                    </Link>
                  </div>
                </div>
              ) : (
                <>
                  <div className="flex flex-col">
                    <span className="text-gray-600 text-xs font-bold">
                      Filter by position name
                    </span>
                    <input
                      onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                        this.setState({ searchPositionInput: e.target.value })
                      }
                      type="text"
                      className="mb-4 bg-gray-100 px-3 py-2 rounded"
                      placeholder="Keyword..."
                    />
                  </div>
                  {searchData(
                    this.props.positions!,
                    this.state.searchPositionInput
                  ).map((item, key) => (
                    <div
                      key={key + 1}
                      onClick={() => {
                        this.setState({ selected_position: item });
                        this.loadAllApplications(
                          item,
                          this.state.selected_period
                            ? this.state.selected_period
                            : this.state.defaultSelectedPeriod,
                          (status: boolean, errorMessage: string) => {
                            this.setLoadingState(status);
                            if (!isEmptyOrSpaces(errorMessage)) {
                              this.props.PushPopupAlert({
                                title: errorMessage,
                                theme: PopupAlertTheme.warning,
                              });
                            }
                          }
                        );
                        this.setState({
                          selectPosition: false,
                          searchPositionInput: "",
                          applicationListData: null,
                          reportTitle:
                            this.state.selected_position?.position_title +
                            " - Application Report - " +
                            this.state.selected_period
                              ? this.state.selected_period!.session_plan_name
                              : this.state.defaultSelectedPeriod
                              ? this.state.defaultSelectedPeriod!
                                  .session_plan_name
                              : "",
                        });
                      }}
                      className={`${
                        this.state.selected_position?.position_id ===
                        item.position_id
                          ? "bg-blue-100 border-blue-600"
                          : "bg-white border-gray-200"
                      } cursor-pointer rounded  px-3 py-2 border hover:border-blue-500 hover:bg-blue-100 mb-2 group`}
                    >
                      <div className="grid lg:grid-cols-12">
                        <div className="col-span-12 lg:col-span-10">
                          <div className="flex flex-row items-center gap-3">
                            <div>
                              {this.state.selected_position?.position_id ===
                              item.position_id ? (
                                <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                              ) : (
                                <IoBriefcaseOutline className="text-4xl md:text-5xl text-gray-400 group-hover:text-blue-600" />
                              )}
                            </div>
                            <div className="flex flex-col">
                              <span className="text-xs lg:text-sm font-bold text-gray-700">
                                {item.position_title}
                              </span>
                              <span className="text-xs text-gray-500 italic">
                                School level:{" "}
                                <span
                                  className="text-gray-800 font-light"
                                  style={{ fontStyle: "normal" }}
                                >
                                  {item.school_level_id.school_level_name}
                                </span>
                              </span>
                              <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">
                                {
                                  item.education_institute_id
                                    .education_category_title
                                }
                              </span>
                            </div>
                          </div>
                        </div>
                        {this.state.selected_position?.position_id !==
                          item.position_id && (
                          <div className="col-span-12 lg:col-span-2 hidden lg:block">
                            <div className="h-full flex items-center justify-end">
                              <div className="btn bg-gray-100 group-hover:bg-blue-600 text-blue-800 group-hover:text-white float-right">
                                Choose
                              </div>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  ))}
                </>
              )}
            </div>
          </Modal>
        )}
        {/* END PERIOD && POSITION CONTENT MODAL */}

        {/* APPLICATION DETAIL CONTENT MODAL */}
        {this.state.applicationDetailsModal !== null &&
          this.state.applicationDetailsModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-extrabold text-2xl text-blue-700">
                      {
                        this.state.applicationDetailsModal.selected_application
                          .fname
                      }{" "}
                      {
                        this.state.applicationDetailsModal.selected_application
                          .lname
                      }
                    </span>
                    <div className="rounded-full text-base border border-blue-600 px-3 py-1 w-max font-bold">
                      Position: {this.state.selected_position?.position_title}
                    </div>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ applicationDetailsModal: null })}
              backDropClose={true}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              padding={{ title: true }}
            >
              {this.props.positions === null ? (
                "No positions found!"
              ) : this.props.sessionPlan.allSessionPlans === null ? (
                "No session found!"
              ) : (
                <ApplicationDetails
                  selectedApplication={
                    this.state.applicationDetailsModal.selected_application
                  }
                  auth={this.props.auth}
                  positions={this.props.positions}
                  getDistrictName={this.GetDistrictName}
                  sessionPlan={this.props.sessionPlan.allSessionPlans}
                />
              )}
            </Modal>
          )}
        {/* END APPLICATION DETAIL CONTENT MODAL */}

        {/* GENERATE DYNAMIC REPORT CONTENT MODAL */}
        {this.state.generateReportModal !== null &&
          this.state.generateReportModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Generating Report contents
                    </span>
                    <span className="text-sm text-blue-600 font-normal">
                      Generating {this.state.generateReportModal.reportType}{" "}
                      Report
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ generateReportModal: null })}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-6">
                <div className="my-2 text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col">
                    <label>Title</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block ${
                        this.state.formError &&
                        this.state.formError.element === "report_title"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder="Sub Category Title"
                      value={this.state.reportTitle}
                      onChange={(e) => {
                        this.setState({
                          formError: null,
                          reportTitle: e.target.value,
                        });
                      }}
                      disabled={this.state.loading}
                    />
                    {this.state.formError &&
                      this.state.formError.element === "report_title" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col mt-2">
                    <label>Report content</label>
                    <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                      {this.state.reportContent.map((item, index) => (
                        <div
                          key={index}
                          className={`flex items-center gap-1 ${
                            this.isChangeAllowed(item.key)
                              ? "cursor-pointer"
                              : "cursor-not-allowed"
                          }`}
                          onClick={() =>
                            this.isChangeAllowed(item.key) &&
                            this.updateReportContent(item.key)
                          }
                          title={
                            !this.isChangeAllowed(item.key)
                              ? "Required for every report!"
                              : ""
                          }
                        >
                          {item.status ? (
                            <MdCheckBox className="w-6 h-6 text-blue-700" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="w-6 h-6" />
                          )}
                          <span>{item.title}</span>
                        </div>
                      ))}
                    </div>
                    {this.state.formError &&
                      this.state.formError.element === "report_content" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="flex justify-between mb-2 mt-4">
                  <div
                    className={`flex items-center gap-1 cursor-pointer`}
                    onClick={() =>
                      this.resetReportContent(this.state.selectAll)
                    }
                  >
                    {this.state.selectAll ? (
                      <MdCheckBox className="w-6 h-6 text-blue-700" />
                    ) : (
                      <MdCheckBoxOutlineBlank className="w-6 h-6" />
                    )}
                    <span>Select all</span>
                  </div>

                  {this.state.applicationsToPrint && !this.state.loading && (
                    <ExportToExcel
                      fileData={this.state.applicationsToPrint}
                      fileName={this.state.reportTitle}
                      btnName="Generate"
                    />
                  )}
                </div>
              </div>
            </Modal>
          )}
        {/* END GENERATE DYNAMIC REPORT CONTENT MODAL */}
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  positions: PositionAPI[] | null;
  sessionPlan: SessionPlanReducerInterface;
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    positions: system_settings.systemPositions,
    sessionPlan,
    schoolLevels: schoolLevels,
  };
};
export const Applications = connect(mapStateToProps, {
  FC_GetAllSessionByInstitution,
  FC_GetPositionsByInstitution,
  loadEducationCategories,
  PushPopupAlert,
})(_Applications);
