import React, { useState } from "react";
import {
  MdCheckCircle,
  MdRadioButtonUnchecked,
  MdSearch,
} from "react-icons/md";
import Loading from "../../components/Loading/Loading";
import {
  PlansInterface,
  SessionPlanInterface,
  SessionPlanTypeStatus,
} from "../../interfaces";
import { DATE } from "../../utils/functions";
import searchData from "../../utils/search";

export const isPlansActive = (plans: PlansInterface[]): PlansInterface[] => {
  let today = new Date();
  let activePlans: PlansInterface[] = [];
  for (let plan of plans) {
    let startDate = new Date(plan.start_date);
    let endDate = new Date(plan.end_date);
    if (
      startDate.getTime() <= today.getTime() &&
      endDate.getTime() >= today.getTime() &&
      plan.type_status === SessionPlanTypeStatus.OPEN
    ) {
      activePlans = [...activePlans, plan];
    }
  }
  return activePlans;
};

